body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #FFFFFF;
}

.App {
  text-align: center;
}

.controls {
  margin: 20px auto 40px auto;
  width: 100%;
}
.Tile {
  height: 100%;
  box-sizing: border-box;
  float: left; }

.TileFlasher {
  height: 100%; }
  .TileFlasher.playing {
    -webkit-animation-name: tileFlash;
            animation-name: tileFlash;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; }

@-webkit-keyframes tileFlash {
  0% {
    background-color: rgba(255, 255, 255, 0.5); }
  100% {
    background-color: rgba(255, 255, 255, 0); } }

@keyframes tileFlash {
  0% {
    background-color: rgba(255, 255, 255, 0.5); }
  100% {
    background-color: rgba(255, 255, 255, 0); } }

.Row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%; }

.RowInner {
  overflow: hidden;
  margin: 0 auto;
  -webkit-flex: 1 1;
          flex: 1 1;
  background: #212121; }

.compButton {
  opacity: 0;
  height: 100%;
  background: #212121;
  border-radius: 10px;
  color: white;
  font-family: Helvetica;
  font-weight: 500px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: white;
  font-size: 20px; }
  @media (min-width: 601px) {
    .compButton:hover {
      opacity: 1;
      cursor: pointer; } }

.Board {
  margin: 0 auto; }

.BoardInner {
  position: relative; }

.AboutModal {
  font-family: Lato;
  position: relative;
  box-sizing: border-box;
  background-color: #F70F0F;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: 10px 5px 15px #212121;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0 auto;
  height: 400px;
  width: 100%;
  max-width: 400px;
  padding: 40px;
  outline: none;
  background: white;
  text-align: center; }
  .AboutModal > * {
    -webkit-flex: 1 1;
            flex: 1 1; }
  .AboutModal.mobile {
    padding: 40px 20px; }

